import React, { Component } from 'react';
import bindu from './img/bindu.jpg';
import saef from './img/saef.jpg';

var createReactClass = require('create-react-class');
var Profile = createReactClass({
  render: function () {
    return (
          <div id="team" className="col-sm-6 board">
              <div className="card">
                  <div className="cardheader"></div>
                  <div className="card-img-box">
                    <img src={this.props.photo} alt="..." className="card-img" />
                      <h4>{this.props.name}</h4>
                      <p>{this.props.info}</p>
                      <blockquote className="blockquote">
                          <i className="quote">{this.props.quote}</i>
                      </blockquote>
                       {this.props.git ? (
                          <a target="_blank" href={this.props.git}>
                            <i className="fa fa-github fa-lg c-fa" aria-hidden="true"></i>
                          </a>
                        ) : (
                          <p></p>
                        ) 
                      }
                      {this.props.twitter ? (
                          <a target="_blank" href={this.props.twitter}>
                            <i className="fa fa-twitter fa-lg c-fa" aria-hidden="true"></i>
                          </a>
                        ) : (
                          <p></p>
                        ) 
                      }
                      {this.props.linkedin ? (
                          <a target="_blank" href={this.props.linkedin}>
                            <i className="fa fa-linkedin fa-lg c-fa" aria-hidden="true"></i>
                          </a>
                        ) : (
                          <p></p>
                        ) 
                      }
                  </div>
                </div>
            </div>
    );
  }
});


class Board extends Component {
  render() {
    return (
      <div className="container board-container">
        <h1 className="font-light center">Director Board</h1>
        <div className="row board-row m-center">
          <Profile
            photo={bindu}
            name="Bindu Jamal"
            info="Director"
            quote="A tech enthusiast that's what he is known for. With educational background in advertising, he is working on modernizing Prochito IMC LTD. with the help of technology.He is one of the dreamer of Prochito ITS."
            git=""
            twitter=""
            linkedin=""
          />
          <Profile
            photo={saef}
            name="Mohammad Saef Ullah Miah"
            info="Director"
            quote="He is committed to direct the team to fulfill the goal. He has great analytical power and decision making skills.he encourages his team to think different. He is one of the dreamer of Prochito ITS."
            git=""
            twitter=""
            linkedin=""
          />

        </div>

  </div>
    );
  }
}

export default Board;



