import React, { Component } from 'react';
import usr from './img/usr.jpg';
import reviewer_a from './img/sabina-reviewer.jpg';


var createReactClass = require('create-react-class');
var Profile = createReactClass({
  render: function () {
    return (
        <div id="review" className="container">
          <div className="row">
            <div className="col-sm-2">
              <img className="reviewer-photo" src={this.props.photo} alt=".." />
            </div>
            <div className="col-sm-10 review-content">
              <span className="font-xl font-light">{this.props.name}</span>
              <span className="font-l"><b>{this.props.rank}</b></span>
              <p>{this.props.note}</p>
            </div>
          </div>
        </div>
    );
  }
});


class Review extends Component {
  render() {
    return (
      <div className="container-fluid review-container">
        <h1 className="font-light center">Reviews</h1>
        <ul id="news-slider">
          <li>
            <Profile
              photo={reviewer_a}
              name="Sabina Yeasmeen"
              rank="Chairman of Prochito IMC Ltd."
              note="We are proud to have our own software division."
            />
          </li>
          <li>
            <Profile
              photo={usr}
              name="iTV USA"
              rank="Online TV Streaming Company, USA"
              note="Prochito ITS was nothing short of amazing! They always set expectations, followed up very often, explained things in a non-technical way, and were a pleasure to work with."
            />
          </li>
          <li>
            <Profile
              photo={usr}
              name="Asiakhabar24"
              rank="Online News Portal, Bangladesh"
              note="Prochito ITS is a great team that delivered amazing results with fast & minimal turn arounds."
            />
          </li>
        </ul>
      </div>
    );
  }
}

export default Review;



