import React, { Component } from 'react';

class Header extends Component {
  render() {
    return (
      <header>
        <div className="navbar-fixed-top">
            <nav id="top-navbar" className="navbar navbar-default">
                <div className="container">
                    <div className="navbar-header">
                        <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#collapse-trigger" aria-expanded="false">
                            <span className="sr-only">Toggle navigation</span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                        </button>

                        <a className="navbar-brand font-light" href="heroContainer.js">PROCHITO ITS</a>
                    </div>
                    <div className="collapse navbar-collapse" id="collapse-trigger">
                        <ul className="nav navbar-nav navbar-right">
                            <li><a href="#hero">HOME</a></li>
                            <li><a href="#vision">VISION</a></li>
                            <li><a href="#technology">TECHNOLOGY</a></li>
        					<li><a href="#skill">SKILLS</a></li>
                            <li><a href="#team">TEAM</a></li>
                            <li><a href="#review">REVIEW</a></li>
                            <li><a href="#map">CONTACT</a></li>
                        </ul>
                    </div>
                </div>
            </nav>
        </div>
      </header>
    );
  }
}

export default Header;



