import React, { Component } from 'react';
import mobileImg from './img/mobile.png';

class HeroContainer extends Component {
  render() {
    return (
      <div id="hero" className="container-fluid hero-container">
      	<section className="hero-circle-group">
      		<div className="hero-circle-1"></div>
      		<div className="hero-circle-2"></div>
      		<div className="hero-circle-3"></div>
      	</section>
      	{/* inside-container */}
      	<div className="container">
      		<div className="row">
      			<div className="col-sm-6">
      				<img className="img-responsive hero-image" src={mobileImg} alt=".." />
      			</div>
      		{/* inside-container-end */}
      		{/* hero-text */}
      			<div className="col-sm-6">
      				<section className="hero-text-container">
      					<h1 className="hero-text-h1"><b>amujamu.com</b></h1>
                <h1 className="hero-text-h1">tour marketplace based on Thailand.</h1>
      					<p className="hero-p">Amujamu is an online service that enables users to research, find, and
      						book travel experiences worldwide. Amujamu offers a variety of tours, tickets and services
      						made available by Amujamu's third party suppliers through Amujamu's and its
      						affiliates' websites and mobile properties and related applications.</p>
      					<section className="hero-btn-section">
      						<button className="btn hero-btn"><i className="fa fa-globe fa-lg" aria-hidden="true"></i> Website</button>
      						<button className="btn hero-btn"><i className="fa fa-mobile fa-lg" aria-hidden="true"></i> Mobile apps</button>
      					</section>
      				</section>
      			</div>
      		</div>
      	</div>
      	{/* hero-text-end */}
      </div>
    );
  }
}

export default HeroContainer;



