import React, { Component } from 'react';
import rushow from './img/rushow.jpg';
import rony from './img/rony.jpg';
import santow from './img/santow.jpg';
import salman from './img/salman.jpg';
import mahbub from './img/mahbub.jpg';
import bari from './img/bari.jpg';
import zahin from './img/zahin.jpg';
import alam from './img/alam.jpg';
import shahrof from './img/usr.jpg';
import join from './img/join.jpg';


var createReactClass = require('create-react-class');
var Profile = createReactClass({
  render: function () {
    return (
          <div className="col-sm-3">
              <div className="card">
                  <div className="cardheader"></div>
                  <div className="card-img-box">
                    <img src={this.props.photo} alt="..." className="card-img" />
                      <h4>{this.props.name}</h4>
                      <p>{this.props.info}</p>

                       {this.props.quote=="Drop CV" ? (
                          <div className="join_us_box">
                            <a target="_blank" className="join_us" href="mailto:career.prochito@gmail.com">Drop your CV</a>
                          </div>
                        ) : (
                          <blockquote className="blockquote">
                            <i className="quote">{this.props.quote}</i>
                          </blockquote>
                        ) 
                      }
                

                      {this.props.git ? (
                          <a target="_blank" href={this.props.git}>
                            <i className="fa fa-github fa-lg c-fa" aria-hidden="true"></i>
                          </a>
                        ) : (
                          <p></p>
                        ) 
                      }
                      {this.props.twitter ? (
                          <a target="_blank" href={this.props.twitter}>
                            <i className="fa fa-twitter fa-lg c-fa" aria-hidden="true"></i>
                          </a>
                        ) : (
                          <p></p>
                        ) 
                      }
                      {this.props.linkedin ? (
                          <a target="_blank" href={this.props.linkedin}>
                            <i className="fa fa-linkedin fa-lg c-fa" aria-hidden="true"></i>
                          </a>
                        ) : (
                          <p></p>
                        ) 
                      }


                  </div>
                </div>
            </div>
    );
  }
});


class Team extends Component {
  render() {
    return (
      <div className="container team-container">
        <h1 className="font-light center">Our Team</h1>
        <div className="row team-row">
            <Profile
              photo={rushow}
              name="MD Moman Ul Haque Khan"
              info="Sr. Software Engineer"
              quote="Breaking the barrier by being a technology enthusiast"
              git=""
              twitter=""
              linkedin="https://www.linkedin.com/in/md-moman-ul-haque-khan-8358a642/"
            />
            <Profile
              photo={rony}
              name="Rony Debnath"
              info="Software Engineer"
              quote="Repeatedly fixing what others unintensionally broke!"
              git="https://github.com/ronydebnath"
              twitter="https://twitter.com/rdnrony"
              linkedin="https://www.linkedin.com/in/ronydebnath"
            />
            <Profile
              photo={santow}
              name="Ashab Santow"
              info="Content Writer English"
              quote="Writer by passion & profession"
              git=""
              twitter=""
              linkedin="https://www.linkedin.com/in/ashab-santow-19833ba8/"
             />
             <Profile
              photo={salman}
              name="Salman Rahman Desh"
              info="Front End Developer"
              quote="Love to develop my design and keen have interest on UX research"
              git="www.github.com/srdesh"
              twitter="www.twitter.com/Salman_Desh"
              linkedin="www.linkedin.com/salmanrahmandesh"
             />
          </div>

          <div className="row team-row">
             <Profile
               photo={mahbub}
               name="Mahbubur Rahman"
               info="Software Developer"
               quote="I love to code for Android"
               git="https://gitlab.com/MahbubHabib"
               twitter="https://twitter.com/MahbubMahbubur"
               linkedin="https://www.linkedin.com/in/mahbubhabib "
             />
             <Profile
               photo={bari}
               name="Arshnul Bari"
               info="SEO"
               quote="Still searching the secrets of SEO"
               git=""
               twitter=""
               linkedin="www.linkedin.com/in/arshnul-bari"
             />
             <Profile
               photo={zahin}
               name="Abrar Zahin"
               info="Jr. Software Quality Tester"
               quote="A QA professional in a developer's body!"
               git="https://github.com/zahin-abrar"
               twitter="https://twitter.com/AbrarAiub"
               linkedin="https://www.linkedin.com/in/abrar-zahin"
              />
              <Profile
               photo={alam}
               name="S. M. Nur Alam"
               info="Software tester"
               quote="I always learn from mistakes of others who take my advice"
               git="https://github.com/nur9382alam"
               twitter=""
               linkedin=""
              />
          </div>

          <div className="row team-row">
            <Profile
              photo={join}
              name="Want to join our team?"
              quote="Drop CV"
            />
         </div>

  </div>
    );
  }
}

export default Team;



