import React, { Component } from 'react';

class Footer extends Component {
  render() {
    return (
      <div>
        <div className="container-fluid footer">
          <div className="container">
            
            <div className="row subscribe">
              <div className="col-sm-8">
                <h4>Have Question?</h4>
                <p>For any query feel free to contact us!</p>
              </div>
              <div className="col-sm-4 m-center">
                <a className="email_us" href="mailto:contact@prochito.com">Email us</a>
              </div>
            </div>

            <div className="row custom-footer">
                  <div className="col-sm-3">
                    <ul className="footer-list">
                      <li><a className="small" href="#hero"> Home</a></li>
                      <li><a className="small" href="#vision"> Vision</a></li>
                      <li><a className="small" href="#technology"> Technology</a></li>
                    </ul>
                  </div>
                  <div className="col-sm-3">
                    <ul className="footer-list">
                      <li><a className="small" href="#skill"> Skills</a></li>
                      <li><a className="small" href="#team"> Team</a></li>
                      <li><a className="small" href="#review"> Review</a></li>
                    </ul>
                  </div>
                  
                  <div className="col-sm-3">
                    <ul className="footer-list">
                      <li><a className="small" href="#map"> Contact</a></li>
                      <li><a target="_blank" className="small" href="https://www.facebook.com/prochitoits/"><i className="fa fa-facebook" aria-hidden="true"></i> Facebook</a></li>
                    </ul>
                  </div>
                  
                  <div className="col-sm-3">
                    <p className="copyright">© 2017, PROCHITO IT SOLUTION ALL RIGHTS RESERVED</p>
                  </div>
                </div>
            </div>
        </div>
      </div>
    );
  }
}

export default Footer;



