import React, { Component } from 'react';

class Skill extends Component {
  render() {
    return (
      <div id="skill" className="container-fluid team-container">

        <div className="container skillbar-container">
          <h1 className="font-light center">Our Skills</h1>
          <div className="row">
            <div className="col-sm-6">
              <div id="ed">Enterprise development</div>
            </div>
            <div className="col-sm-6">
              <div id="ec">E-commerce</div>
            </div>
            <div className="col-sm-6">
              <div id="erpd">ERP diagram</div>
            </div>
            <div className="col-sm-6">
              <div id="sqt">Software quality testing</div>
            </div>
            <div className="col-sm-6">
              <div id="ma">Mobile application</div>
            </div>
            <div className="col-sm-6">
              <div id="uiux">UI/UX designing</div>
            </div>
          </div>
        </div>

      </div>
    );
  }
}

export default Skill;



